@import "styles/variables.scss";

.products > div {
    padding: 16px 0 16px;
}

button.button {
    width: 100%;
}

.productsBack {
    padding-bottom: 65px;
}

.productsBack > div:first-child > div:last-child {
    margin-bottom: 24px;
}

.productsBack > p {
    font-size: 16px;
    line-height: 125%;
    letter-spacing: -0.02em;
    font-feature-settings: 'zero' on;
    margin-bottom: 16px;
}

.productsBack > div.textarea {
    margin-top: 16px;
    padding-bottom: 24px;
    border-bottom: 1px solid rgba(35, 35, 35, 0.3);
}

.location {
    width: 100%;
    margin-top: 16px;
}

.location > div {
    width: 100%;
    margin-bottom: 24px;
}

.location > p {
    line-height: 125%;
    letter-spacing: -0.02em;
    font-feature-settings: 'zero' on;
    margin-bottom: 16px;
    font-size: 16px;
}

.sendBtn {
    margin-top: 32px;
}

.successSendText {
    line-height: 125%;
    letter-spacing: -0.02em;
    font-feature-settings: 'zero' on;
    margin-bottom: 24px;
}

.successSendText > a {
    text-decoration: none;
    border-bottom: 1px solid $darkBlue;
}

.withUnderline {
    padding-bottom: 32px;
    margin-bottom: 32px;
    border-bottom: 1px solid rgba(35, 35, 35, 0.3);
}

.successSendTitle {
    text-transform: uppercase;
    margin-bottom: 24px;
}

.subTitle {
    margin-bottom: 4px;
}

.bigSubTitle {
    margin-bottom: 8px;
}

@media #{$tablet} {
    .products > div:last-child {
        margin-bottom: 24px;
        border-bottom: 1px solid rgba(35, 35, 35, 0.3);
    }
    .productsBack {
        padding-bottom: 35px;
    }
    .productsBack > div:first-child > div {
        padding-bottom: 16px;
    }

    .productsBack > div:first-child > div:last-child {
        margin-bottom: 24px;
        border-bottom: 1px solid rgba(35, 35, 35, 0.3);
    }
    .productsBack > div.textarea {
        padding-bottom: 16px;
    }
    .location > div {
        margin-bottom: 16px;
    }
    .sendBtn {
        margin-top: 24px;
    }
    .successSendText:nth-child(3) {
        margin-bottom: 24px;
    }
    .successSendText {
        margin-bottom: 16px;
    }
    .withUnderline {
        padding-bottom: 24px;
        margin-bottom: 24px;
    }
    .successSendTitle {
        margin-bottom: 16px;
    }
    .subTitle {
        margin-bottom: 4px;
    }
    
    .bigSubTitle {
        margin-bottom: 8px;
    }
}
