@import 'styles/variables.scss';

.title {
    max-width: 260px;
    line-height: 120%;
}

.datepicker,
.phone {
    border: none;
    border-bottom: 1px solid rgba(27, 37, 48, 0.5);
    background-color: $beige;
    padding: 0 0 8px;
    width: 100%;
    letter-spacing: -0.02em;
    font-feature-settings: 'zero' on;
    line-height: 125%;
    font-size: 16px;
}

.datepicker:focus-visible,
.phone:focus-visible {
    outline: none;
    border-bottom: 1px solid rgba(27, 37, 48, 1);
}

.datepicker::placeholder,
.phone::placeholder {
    color: $darkBlue;
    opacity: 0.4;
}

.input {
    margin-bottom: 24px;
}

.phone {
    margin-bottom: 32px;
}

.phone.phoneTimer {
    margin-bottom: 24px;
}

button.button {
    width: 100%;
}

.firstBtn {
    margin-bottom: 8px;
}

div.switcher {
    justify-content: space-between;
    width: 100%;
    margin-top: 32px;
    padding-top: 16px;
    border-top: 1px solid rgba(35, 35, 35, 0.3);
}

.back {
    position: absolute;
    top: -56px;
    left: 0;
    font-size: 16px;
    background-color: $beige;
    text-transform: uppercase;
    cursor: pointer;
}

.isError {
    margin-bottom: 10px;
}

.isError + p {
    margin-top: 0;
    transform: translateY(-6px);
}

div.isErrorSmsCode + p {
    left: 0;
    top: 30px;
}

.datepickerBlock,
.phoneBlock {
    position: relative;
}

.datepickerBlock > img,
.phoneBlock > img {
    position: absolute;
    right: 0;
    top: 4px;
}

.phoneBlock > p {
    position: absolute;
    top: 10%;
    right: 0;
    font-size: 12px;
    cursor: pointer;
}

.smsInput {
    margin-bottom: 32px;
}

.phoneBlock > p.codeField {
    right: 0;
}

.phoneContainer {
    position: relative;
}

.errorText {
    color: $redColor;
    position: absolute;
    bottom: 6px;
    font-size: 12px;
}

.errorPhone {
    color: $redColor;
    border-bottom: 1px solid $redColor;
}

.errorTextPhone {
    bottom: 12px;
}

.errorTextPhoneWithCode {
    bottom: 6px;
}

@media #{$mobile} {
    .datepicker,
    .phone {
        line-height: 100%;
    }
}