@import "styles/variables.scss";

.input:not(:last-of-type) {
    margin-bottom: 24px;
}

.inputMask {
    border: none;
    background-color: $beige;
    padding-bottom: 8px;
    width: 100%;
    border-bottom: 1px solid #1B2530;
    margin-bottom: 0;
    letter-spacing: -0.02em;
    font-feature-settings: 'zero' on;
    line-height: 125%;
    box-sizing: border-box;
    padding: 0 0 8px;
}

.inputMask:focus-visible {
    outline: none;
}

.inputMask::placeholder {
    color: $darkBlue;
    opacity: 0.4;
}

.checkboxBlock {
    margin: 24px 0 32px;
}

.checkboxBlock > div:first-child {
    margin-bottom: 4px;
}

.isError {
    margin-bottom: 10px;
}

.isError + p {
    margin-top: 0;
    transform: translateY(-6px);
}

.isErrorPass + p + p {
    margin-top: -14px;
}

.errorText {
    position: absolute;
    margin-top: 4px;
    color: $redColor;
    font-size: 12px;
}

.back {
    position: absolute;
    top: -56px;
    left: 0;
    font-size: 16px;
    background-color: $beige;
    text-transform: uppercase;
    cursor: pointer;
}

button.reg {
    width: 100%;
}

.phoneBlock {
    position: relative;
}

.phoneBlock > p {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-65%);
    font-size: 12px;
    cursor: pointer;
}

.smsInput {
    margin-top: 24px;
}

.errorPhone {
    color: $redColor;
}

@media #{$mobile} {
    .inputMask {
        font-size: 16px;
        line-height: 100%;
    }
    .checkboxBlock {
        margin-bottom: 24px;
    }
}