@import "styles/variables.scss";

.orderInfo {
    border-top: 1px solid rgba(35, 35, 35, 0.3);
    padding-top: 12px;
    margin-bottom: 24px;
    position: relative;
}

.orderInfo > p {
    max-width: 223px;
}

.orderInfo > p:first-child {
    margin-bottom: 27px;
}

.orderInfo > h1 {
    position: absolute;
    top: 12px;
    right: 0;
}

.products > div {
    padding-bottom: 24px;
}

.products > div:last-child {
    margin-bottom: 24px;
}

.price {
    display: flex;
    flex-direction: column;
}

.price > div {
    display: flex;
    justify-content: space-between;
    margin-bottom: 8px;
}

.price > div:last-child {
    margin-bottom: 24px;
}

.link {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-top: 28px;
    cursor: pointer;
}

.link > p {
    font-size: 16px;
    text-transform: uppercase;
    margin: 0 4px 60px 0;
}

.link > img {
    transform: translateY(-3px);
}

.back {
    position: absolute;
    top: -56px;
    left: 0;
    font-size: 16px;
    background-color: $beige;
    text-transform: uppercase;
    cursor: pointer;
}

.cloudpay_button button {
    width: 100%;
}

@media #{$tablet} {
    .orderInfo {
        padding-top: 8px;
        margin-bottom: 16px;
    }
    .products > div {
        padding-bottom: 16px;
    }
    .products > div:last-child {
        border-bottom: 1px solid rgba(35, 35, 35, 0.3);
        margin-bottom: 16px;
    }
}