@import "styles/variables.scss";

.titleDelivery {
    margin-bottom: 12px;
    line-height: 100%;
    text-transform: uppercase;
}

.titleDelivery:nth-last-of-type(2) {
    padding-top: 32px;
    border-top: 1px solid rgba(35, 35, 35, 0.3);
}

.textInfo {
    max-width: 300px;
    margin-bottom: 32px;
}

.textInfo:nth-of-type(2),
.textInfo:nth-of-type(3) {
    margin-bottom: 12px;
}

.back {
    position: absolute;
    top: -56px;
    left: 0;
    font-size: 16px;
    background-color: $beige;
    text-transform: uppercase;
    cursor: pointer;
}

@media #{$tablet} {
    .successSendText:nth-child(5) {
        margin-bottom: 24px;
    }
}