@import "styles/variables.scss";

.radio {
    padding: 16px 0;
    border-top: 1px solid rgba(35, 35, 35, 0.2);
}

.withoutBorder {
    padding: 4px 0;
    border-top: none;
}

.radioGroup > div.radio:last-child {
    border-bottom: 1px solid rgba(35, 35, 35, 0.2);
}

.radioGroup > div.withoutBorder:last-child {
    border-bottom: none;
}

.radio > div {
    display: flex;
    justify-content: space-between;
}

.radio > div > p {
    text-transform: uppercase;
    line-height: 100%;
}

.radio > div > img {
    cursor: pointer;
}

.radio > p {
    max-width: 300px;
    margin-top: 24px;
    margin-left: 40px;
    font-size: 12px;
    line-height: 140%;
    letter-spacing: -0.02em;
    font-feature-settings: 'zero'
}

.error p,
.error label,
.error span {
    color: $redColor;
}

.errorText {
    position: relative;
    top: 4px;
    left: 38px;
    color: $redColor;
    font-size: 12px;
}

.radio > .widjetLink {
    cursor: pointer;
    font-size: 16px;
    border-bottom: 1px solid $darkBlue;
    width: fit-content;
    margin-top: 16px;
}

@media #{$tablet} {
    .radio > div > p {
        font-size: 16px;
    }
}