@import "styles/variables.scss";

.favorites {
    display: flex;
    flex-direction: column;
    position: relative;
}

.favorites > h2 {
    margin-left: 50%;
    transform: translateX(-50%);
    white-space: nowrap;
    width: fit-content;
}

.button {
    display: block;
    min-width: 100%;
    position: absolute;
    bottom: 40px;
}

.button > button {
    width: 100%;
}

.back {
    position: absolute;
    top: -56px;
    left: 0;
    font-size: 16px;
    background-color: $beige;
    text-transform: uppercase;
    cursor: pointer;
}