@import "styles/variables.scss";

.lk {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: calc(100vh - 112px);
}

.lk .link:last-child {
    margin-top: 24px;
}

.link {
    display: flex;
    justify-content: space-between;
    width: fit-content;
    cursor: pointer;
}

.link > p {
    font-size: 16px;
    text-transform: uppercase;
    margin: 0 4px 20px 0;
}

.link > img {
    transform: translateY(-3px);
}

button.button {
    width: 100%;
    margin-top: 40px;
}

.lk > div:last-child > p {
    font-size: 16px;
    letter-spacing: -0.02em;
    font-feature-settings: 'zero' on;
    line-height: 125%;
    max-width: 400px;
}

.lk > div:last-child > :nth-child(2) {
    margin-top: 16px;
}

.lk > div:last-child > :nth-child(2) > a:last-child {
    text-decoration: none;
}

@media #{$tablet} {
    .lk > div:last-child {
        position: fixed;
        bottom: 40px;
        width: calc(100% - 64px);
    }
    .link > p {
        margin-bottom: 16px;
    }
    .lk > div:last-child > p {
        font-size: 12px;
        max-width: 225px;
    }
    .lk > div:last-child > :nth-child(2) {
        margin-top: 8px;
    }
    .button {
        margin-top: 24px;
    }
}