@import 'styles/variables.scss';

.dropdown {
    position: relative;
    width: fit-content;
}

.dropdata {
    position: absolute;
    width: 100%;
    margin-top: 4px;
    border: 1px solid rgba(35, 35, 35, 0.3);
    background-color: $beige;
    max-height: 96px;
    z-index: 1;
    box-sizing: border-box;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.dropdata::-webkit-scrollbar {
    width: 0;
    height: 0;

}

.dropdata div.dropdataItem {
    padding: 8px 0 8px 8px;
    line-height: 100%;
    cursor: pointer;
}

.selectedFieldContainer {
    position: relative;
}

.selectedFieldContainer > img {
    position: absolute;
    right: 8px;
    top: 50%;
    transform: translateY(-50%);
}

.selectedField {
    width: 66px;
    height: 32px;
    line-height: 32px;
    vertical-align: middle;
    padding-left: 8px;
    border: 1px solid rgba(35, 35, 35, 0.3);
    cursor: pointer;
    box-sizing: border-box;
}

@media #{$tablet} {
    .selectedField {
        white-space: nowrap;
        overflow: hidden;
        padding-right: 10px;
        text-overflow: ellipsis;
    }
}