@import "styles/variables.scss";

.radio > input {
    display: none;
}

.radio > label {
    display: flex;
    text-transform: uppercase;
    line-height: 100%;
    font-size: 16px;
    max-width: 340px;
    cursor: pointer;
}

.radio > input:checked ~ label::before {
    background-image: url(../../../public/static/img/radio.svg);
    background-size: 10px;
    background-repeat: no-repeat;
    background-position: 3px 3px;
}

.radio > label::before {
    content: '';
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 24px;
    min-width: 16px;
    height: 16px;
    border-radius: 50%;
    border: 1px solid $darkBlue;
}

.disable {
    opacity: 0.4;
}

.disable > label {
    cursor: default;
}

@media #{$tablet} {
    .radio > label {
        font-size: 16px;
        max-width: 290px;
    }
}