@import "styles/variables.scss";

.switch {
    display: flex;
    align-items: center;
    position: relative;
    width: fit-content;
}

.switch > p {
    margin-right: 8px;
}

.switch input {
    display: none;
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    display: inline-block;
    position: relative;
    cursor: pointer;
    background-color: $beige;
    -webkit-transition: .4s;
    transition: .4s;
    width: 50px;
    height: 24px;
}

.slider:before {
    position: absolute;
    content: "";
    height: 16px;
    width: 16px;
    left: 3px;
    bottom: 3px;
    background-color: $beige;
    border: 1px solid $darkBlue;
    -webkit-transition: .4s;
    transition: .4s;
}

input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
    background-color: $darkBlue;
}

/* Rounded sliders */
.slider.round {
    border-radius: 50px;
    border: 1px solid $darkBlue;
}

.slider.round:before {
    border-radius: 50%;
}

@media #{$tablet} {
    .switch > p {
        font-size: 16px;
    }
}