@import "styles/variables.scss";

.textarea {
    width: 100%;
    & > textarea {
        width: 100%!important;
        height: 120px;
        resize: none;
        border: 1px solid rgba(35, 35, 35, 0.3);
        font-weight: 400;
        font-size: 16px;
        padding: 16px 16px;
        box-sizing: border-box;
        &:focus {
            outline: none;
        }
    }
    & > p {
        font-weight: 400;
        font-size: 16px;
    }
}

p.errorText {
    color: $redColor;
    font-size: 12px;
}