@import 'styles/variables.scss';

.input {
    margin-bottom: 24px;
}

button.button {
    width: 100%;
}

div.switcher {
    justify-content: space-between;
    width: 100%;
    margin-top: 32px;
    padding-top: 16px;
    border-top: 1px solid rgba(35, 35, 35, 0.3);
}

.back {
    position: absolute;
    top: -56px;
    left: 0;
    font-size: 16px;
    background-color: $beige;
    text-transform: uppercase;
    cursor: pointer;
}

.isError {
    margin-bottom: 10px;
}

.isError + p {
    margin-top: 0;
    transform: translateY(-6px);
}