@import "styles/variables.scss";

.buttonsBlock {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 24px;
}

.buttonsBlock > button {
    width: 100%;
}

.buttonsBlock > button:first-child {
    margin-right: 8px;
}

button.buttonOpasity {
    border: 1px solid rgba(35, 35, 35, 0.2);
}

.inputBlock {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.inputBlock > div:last-child,
.inputBlock > div:nth-child(2) {
    margin-top: 20px;
}

.actions {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
}

.actions > p {
    font-size: 12px;
    cursor: pointer;
}

.signButtonBlock {
    margin-top: 32px;
}

.signButtonBlock button {
    width: 100%;
}

.signButtonBlock > button:first-child {
    margin-bottom: 8px;
}

.imgBlock {
    position: absolute;
    bottom: 0;
}

.description {
    max-width: 400px;
    margin-bottom: 24px;
}

.resetInput {
    margin-bottom: 32px;
}

.resetButton {
    width: 100%;
}

.inputMask {
    border: none;
    background-color: $beige;
    padding-bottom: 8px;
    width: 100%;
    border-bottom: 1px solid #1B2530;
    margin-bottom: 0;
    letter-spacing: -0.02em;
    font-feature-settings: 'zero' on;
    line-height: 125%;
    box-sizing: border-box;
    padding: 0 0 8px;
}

.inputMask:focus-visible {
    outline: none;
}

.inputMask::placeholder {
    color: $darkBlue;
    opacity: 0.4;
}

.phoneBlock {
    position: relative;
}

.phoneBlock > p {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-65%);
    font-size: 12px;
    cursor: pointer;
}

.back {
    position: absolute;
    top: -56px;
    left: 0;
    font-size: 16px;
    background-color: $beige;
    text-transform: uppercase;
    cursor: pointer;
}

.isError {
    margin-bottom: 10px;
}

.isError + p {
    margin-top: -6px;
}

.isErrorRestore ~ p {
    margin-bottom: 8px;
}

.errorText {
    position: absolute;
    margin-top: 35px;
    color: $redColor;
    font-size: 12px;
}

.errorPhone {
    color: $redColor;
    border-bottom: 1px solid $redColor;
}

@media #{$mobile} {
    .buttonsBlock > button:hover {
        background-color: $beige;
    }
    .buttonsBlock > button:hover > pre {
        color: $darkBlue;
    }
    .signButtonBlock {
        margin-top: 24px;
    }
    .description {
        max-width: 275px;
    }
    .resetInput {
        margin-bottom: 24px;
    }
    .inputMask {
        font-size: 16px;
        line-height: 100%;
    }
}