@import "styles/variables.scss";

.stars {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.stars > div {
    display: flex;
}

.stars > p {
    font-size: 16px;
}

.stars img {
    display: block;
}

.textArea {
    margin: 16px 0 32px;
}

button.button,
button.editButton {
    width: 100%;
}

.editButton {
    margin-top: 24px;
}

.product {
    border-bottom: 1px solid rgba(35, 35, 35, 0.3);
    padding-bottom: 16px;
    margin-bottom: 24px;
}

@media #{$tablet} {
    .textArea {
        margin: 16px 0 24px;
    }
}