@import "styles/variables.scss";

.products > h2 {
    margin-left: 50%;
    transform: translateX(-50%);
}

.favoritesItem {
    display: flex;
    position: relative;
    border-top: 1px solid rgba(35, 35, 35, 0.3);
    padding: 16px 0;
    cursor: pointer;
}

.favoritesItem > img {
    border: 1px solid rgba(35, 35, 35, 0.3);
    background-image: url('/static/img/miniLoader.svg');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 50% 50%;
}

.favoritesItem p {
    letter-spacing: -0.02em;
    font-feature-settings: 'zero' on;
    line-height: 125%;
}

.favoritesItem:last-child {
    border-bottom: 1px solid rgba(35, 35, 35, 0.3);
    margin-bottom: 80px;
}

.status {
    position: absolute;
    top: 18px;
    right: 2px;
    cursor: pointer;
}

.status > h2 {
    max-width: 130px;
    text-align: end;
    font-weight: 400;
}

.infoBlock {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-left: 16px;
    max-width: 212px;
    min-height: 100%;
}

.infoBlock > img {
    position: absolute!important;
    bottom: 16px;
    right: 0;
    cursor: pointer;
}

.infoBlock > div > p:first-child {
    margin-bottom: 4px;
}

.back {
    position: absolute;
    top: -56px;
    left: 0;
    font-size: 16px;
    background-color: $beige;
    text-transform: uppercase;
    cursor: pointer;
}

button.loadMore {
    width: 100%;
}

button.payment {
    width: 100%;
    margin: 0px 0 24px 0;
}

.lineBottom {
    width: 100%;
    height: 1px;
    background-color: rgba(35, 35, 35, 0.3);
}

@media #{$tablet} {
    .favoritesItem {
        padding: 8px 0;
    }

    button.payment {
        margin: 0px 0 16px 0;
    }
}