@import "styles/variables.scss";

.lkBGC {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(35, 35, 35, 0.8);
    z-index: 10000000;
}

.lk {
    position: absolute;
    right: 0;
    height: 100vh;
    overflow-y: scroll;
    min-width: 358px;
    width: min-content;
    background-color: $beige;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.lk::-webkit-scrollbar {
    display: none;
    -webkit-appearance: none;
    width: 0;
    height: 0;
}

.lk > img {
    position: fixed!important;
    top: 24px;
    right: 24px;
    cursor: pointer;
    z-index: 1;
}

.content {
    margin: 80px 24px 32px;
    min-height: calc(100vh - 112px);
    position: relative;
}

@media #{$mobile} {
    .lk {
        width: 100vw;
    }
    .lk > img {
        top: 14px;
        right: 14px;
    }
    .content {
        margin: 80px 32px 40px;
    }
}