@import "styles/variables.scss";

.inputBlock {
    position: relative;
}

.inputBlock > img {
    position: absolute!important;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    cursor: pointer;
}

.input {
    border: none;
    border-bottom: 1px solid rgba(27, 37, 48, 0.5);
    background-color: $beige;
    padding: 0 0 8px;
    width: 100%;
    letter-spacing: -0.02em;
    font-feature-settings: 'zero' on;
    line-height: 125%;
    font-size: 16px;
}

.inputWithoutPencil {
    border-bottom: 1px solid rgba(27, 37, 48, 0.5);
}

.inputWithoutPencil:focus-visible {
    border-bottom: 1px solid rgba(27, 37, 48, 1);
}

.inputWithPencil:focus-visible {
    border-bottom: 1px solid rgba(27, 37, 48, 1);
}

.input:focus-visible {
    outline: none;
}

.input::placeholder {
    color: $darkBlue;
    opacity: 0.4;
}

.isError {
    border-bottom: 1px solid $redColor;
}

.isError > input {
    color: $redColor;
}

.errorText {
    margin-top: 4px;
    color: $redColor;
    font-size: 12px;
}

.label {
    transform: translateY(-20px);
    font-size: 12px;
}

@media #{$mobile} {
    .input {
        line-height: 100%;
    }
}